import {
    CollectablesShopRefine,
    Item,
    Jobs,
    Recipe,
    RecipeRequirements,
} from '@/libs/Craft';
import { defineStore } from 'pinia';

export default defineStore('designer', {
    state: () => ({
        content: null as {
            item: Item;
            job?: Jobs;
            recipe: Recipe;
            recipeId?: number;
            materialQualityFactor: number;
            requirements: RecipeRequirements;
            collectability?: CollectablesShopRefine;
            simulatorMode: boolean;
        } | null,
        options: {
            exportOptions: {
                addNotification: <boolean | 'auto'>'auto', // 
                sectionMethod: <'avg' | 'greedy' | 'disable'>'avg', // 
                notifySound: ' <se.1>', // 
                hasLock: false, // 
                waitTimeInc: 0, // ()
                oneclickCopy: true, // 
            },
            importOptions: {
                strictMode: false,
            },
            analyzerOptions: {
                ignoreErrors: true,
            },
        },
    }),
    getters: {
        toJson(): string {
            return JSON.stringify({
                options: this.options,
            });
        },
    },
    actions: {
        selectRecipe(payload: {
            job?: Jobs;
            item: Item;
            recipe: Recipe;
            recipeId?: number;
            materialQualityFactor: number;
            requirements: RecipeRequirements;
            collectability?: CollectablesShopRefine;
            simulatorMode: boolean;
        }) {
            this.content = payload;
        },

        fromJson(json: string) {
            try {
                const v = JSON.parse(json);
                if (v.options) {
                    this.$patch({ options: v.options });
                }
            } catch (e: any) {
                console.error(e);
            }
        },
    },
});
